import React, { useEffect, useState } from 'react';
import './App.css';
import UserCard from './userCard/userCard';

interface UserInterface {
  avatar: string,
  email: string,
  first_name: string,
  id: number,
  last_name: string
}

function App() {

  const [users, setUsers] = useState<UserInterface[]>([]);
  // const [user, setUser] = useState<UserInterface | null>(null);

  useEffect(() => {
    fetch("https://6021303e46f1e40017804036.mockapi.io/users")
      .then(users => users.json())
      .then((users: any) => {
        setUsers(users);
      })
  }, []);

  const updateServiceWorker = () => {
    console.log('+++++++++++++++++++++++++')
    navigator?.serviceWorker.controller?.postMessage({type: 'SKIP_WAITING'});
  }

  return (
    <div className="App">
      <div className="users">
      {users?.map((user: UserInterface) => {
        return (
          <div key={user.id} className="user-card-wrapper">
            <UserCard {...user}></UserCard>
          </div>
        )
      })}
      </div>
      
      <div className="updateContainer" id="notification">
        <div className="updateText">
          A new version of users is available
          <button className="updateButton" onClick={updateServiceWorker}>REFRESH</button>
        </div>
      </div>

    </div>
  );
}

export default App;
